<template>
  <div>
    <div class="contact__title">Contact</div>
    <div class="contact__form__container">
      <div :class="{'name':true, 'showing':!showingSavedMessage}">
        <label for="name">Name</label>
        <input type="text" placeholder="type your name" id="name" class="form__input" v-model="message.name">
        <span class="error__span">{{ errorMessages.name }} </span>
      </div>
      <div :class="{'email':true, 'showing':!showingSavedMessage}">
        <label for="mail">E-mail</label>
        <input type="text" placeholder="type your e-mail" id="mail" class="form__input" v-model="message.email">
        <span class="error__span">{{ errorMessages.email }} </span>
      </div>
      <div :class="{'budget':true, 'showing':!showingSavedMessage}">
        <label for="budget">Budget</label>
        <select id="budget" v-model="message.budget">
          <option value="">select your budget</option>
          <option value="300">300</option>
          <option value="500">600</option>
          <option value="more">600 +</option>
        </select>
        <span class="error__span">{{ errorMessages.budget }} </span>
      </div>
      <div :class="{'description':true, 'showing':!showingSavedMessage}">
        <label for="description">Message</label>
        <textarea id="description" placeholder="tell me about your idea" v-model="message.description"></textarea>
        <span class="error__span">{{ errorMessages.description }} </span>
      </div>
      <div :class="{'btn__container':true, 'showing':!showingSavedMessage}">
        <button type="submit" @click="saveMessage()" class="btn__submit__form">{{buttonText}}</button>
      </div>
    </div>
    <div :class="{'saved__message':true, 'showing_message':showingSavedMessage}">
      ¡Thank you for message, I'll contact you soon!
    </div>
  </div>
</template>

<script>
  import * as firebase from 'firebase';
  import Validate from 'validate.js';

  export default {
    name: "Contact",
    mounted() {
      var title = document.querySelector('.contact__title');
      setTimeout(() => {
        title.classList.add('showing');
      }, 300);
      var form = document.querySelector('.contact__form__container');
      setTimeout(() => {
        form.classList.add('showing');
      }, 300);

    },
    data() {
      return {
        buttonText:'¡Contact!',
        message: {
          name: '',
          email: '',
          budget: '',
          description: ''
        },
        errorMessages: {
          name: '',
          email: '',
          budget: '',
          description: ''
        },
        showingSavedMessage: false,

      }
    },
    methods: {
      saveMessage() {
        var vm = this;
        var constraints = vm.getValidationConstraints();
        var errorFields = Object.keys(constraints);
        var errors = Validate.validate(vm.message, constraints, {fullMessages: false});
        vm.errorMessages = {
          name: '',
          email: '',
          budget: '',
          description: ''
        };
        if (errors) {
          var errorKeys = Object.keys(errors);
          errorKeys.forEach((value) => {
            vm.errorMessages[value] = errors[value].join("; ");
          });
          return;
        }
        vm.buttonText = 'Please Wait...';
        firebase.database().ref('messages').push(this.message)
          .then((response) => {
            this.message = {
              name: '',
              email: '',
              budget: '',
              description: ''
            };
            this.showingSavedMessage = true;
            setTimeout(() => {
              vm.showingSavedMessage = false;
              vm.buttonText = '¡Contact!';
            }, 4000);

          })
          .catch((error) => console.log(error));
      },
      getValidationConstraints() {
        return {
          name: {
            presence: {allowEmpty: false, message: "name field can't be empty"},
          },
          email: {
            presence: {allowEmpty: false, message: "email field can't be empty"},
            email: {
              message: "doesn't look like a valid email"
            }
          },
          budget: {
            presence: {allowEmpty: false, message: "budget field can't be empty"},
          },
          description: {
            presence: {allowEmpty: false, message: "description field can't be empty"},
            length: {
              minimum: 20,
              message: " tell me more about your idea (20) letters min"
            }
          }
        }
      },
    }

  }
</script>

<style scoped>
  .contact__form__container {
    width: 500px;
    height: 400px;
    box-sizing: border-box;
    padding: 3% 0;
    margin: 0 auto;
    transform: translateY(-20px);
    opacity: 0;
    -webkit-transition: all .7s ease-in-out;
    -moz-transition: all .7s ease-in-out;
    -ms-transition: all .7s ease-in-out;
    -o-transition: all .7s ease-in-out;
    transition: all .7s ease-in-out;
  }

  .contact__form__container.showing {
    transform: translateY(0);
    opacity: 1;
  }

  .contact__form__container div label {
    cursor: pointer;
    display: block;
    margin: 0 15px;
  }

  .contact__form__container div {
    margin: 10px;
  }

  input, textarea, select {
    border-radius: 5px;
    background: transparent;
    padding: 8px;
    border: none;
    border-bottom: 1px solid #000;
    width: 100%;
    -webkit-transition: all .1s;
    -moz-transition: all .1s;
    -ms-transition: all .1s;
    -o-transition: all .1s;
    transition: all .1s;
  }

  input:focus, textarea:focus, select:focus {
    border-bottom: 1px solid #4900af;
    background: #e6e6e6;
  }

  .contact__title {
    text-align: center;
    padding: 10px 0;
    font-size: 35px;
    font-weight: bold;
    opacity: 0;
    transform: translateY(-50px);
    -webkit-transition: all .7s ease;
    -moz-transition: all .7s ease;
    -ms-transition: all .7s ease;
    -o-transition: all .7s ease;
    transition: all .7s ease;
  }

  textarea {
    resize: vertical;
    min-height: 100px;
  }

  .btn__container {
    width: 100%;
    text-align: center;
  }

  .btn__submit__form {
    margin-top: 10px;
    padding: 15px 10px;
    width: 50%;
    background: transparent;
    border: 1px solid #000;
    border-radius: 30px;
    -webkit-transition: all .1s ease-in-out;
    -moz-transition: all .1s ease-in-out;
    -ms-transition: all .1s ease-in-out;
    -o-transition: all .1s ease-in-out;
    transition: all .1s ease-in-out;
  }

  .btn__submit__form:hover {
    cursor: pointer;
    background: #000;
    color: #fff;
  }

  .error__span {
    font-size: 13px;
    color: #d71c1e;
  }

  .name,
  .email,
  .budget,
  .description,
  .btn__container {
    transform: translateX(-100px);
    opacity: 0;
  }

  .name {
    -webkit-transition: all .7s ease-in-out;
    -moz-transition: all .7s ease-in-out;
    -ms-transition: all .7s ease-in-out;
    -o-transition: all .7s ease-in-out;
    transition: all .7s ease-in-out;
  }

  .email {
    -webkit-transition: all .9s ease-in-out;
    -moz-transition: all .9s ease-in-out;
    -ms-transition: all .9s ease-in-out;
    -o-transition: all .9s ease-in-out;
    transition: all .9s ease-in-out;
  }

  .budget {
    -webkit-transition: all 1.2s ease-in-out;
    -moz-transition: all 1.2s ease-in-out;
    -ms-transition: all 1.2s ease-in-out;
    -o-transition: all 1.2s ease-in-out;
    transition: all 1.2s ease-in-out;
  }

  .description {
    -webkit-transition: all 1.5s ease-in-out;
    -moz-transition: all 1.5s ease-in-out;
    -ms-transition: all 1.5s ease-in-out;
    -o-transition: all 1.5s ease-in-out;
    transition: all 1.5s ease-in-out;
  }

  .btn__container {
    -webkit-transition: all 1.8s ease-in-out;
    -moz-transition: all 1.8s ease-in-out;
    -ms-transition: all 1.8s ease-in-out;
    -o-transition: all 1.8s ease-in-out;
    transition: all 1.8s ease-in-out;
  }

  .saved__message {
    width: 500px;
    box-sizing: border-box;
    text-align: center;
    font-size: 42px;
    position: absolute;
    top: 28%;
    right: 20%;
    transform: translateX(200px);
    opacity: 0;
    -webkit-transition: 1s ease-in;
    -moz-transition: 1s ease-in;
    -ms-transition: 1s ease-in;
    -o-transition: 1s ease-in;
    transition: 1s ease-in;
  }

  .saved__message.showing_message {
    transform: translateX(0);
    opacity: 1;
  }

  .showing {
    transform: translateX(0);
    opacity: 1;
  }

  .showing__title {
    transform: translateY(0);
    opacity: 1;
  }

  .slide-fade-enter-active {
    transition: all .3s ease;
  }

  .slide-fade-leave-active {
    transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }

  .slide-fade-enter, .slide-fade-leave-to
    /* .slide-fade-leave-active below version 2.1.8 */
  {
    opacity: 0;
  }
</style>



